$rwdMaxWidth: 820px;


.section {
    &:hover .pointUppercaseName {
        background-color: #7597c1;
    }
    &:hover .pointName {
        color: #a4c1ff;
    }
    &:hover .pointIntern {
        color: #d2abff;
    }
    &:hover .pointTech {
        color: #f693ff;
    }
    &:hover .pointDream {
        color: #fc9ead;
    }
    &:hover .pointHobbies {
        color: #fdd9c6;
    }

    & .div {
        margin: 0 auto;
        width: 810px;

        display: flex;
        align-items: center;
        flex-direction: row;


        @media (max-width: $rwdMaxWidth) {
            flex-direction: column;
        }

        & section {
            margin: 10px 25px;


            & article {
                margin: 10px 0;
                width: 510px;

                font-family: FreeMono, monospace;


                @media (max-width: 510px) {
                    width: unset;
                }

                & span {
                    transition: color .2s linear, background-color .3s linear;
                }

                & .pointUppercaseName {
                    border-radius: 3px;
                }
            }
        }
        & #personalPhoto {
            width: 200px;

            border-radius: 100px;
            box-shadow: 20px 20px 30px rgb(26, 26, 26);
        }
    }
}