.div {
    & .nav {
        padding: 0 10px;
        display: flex;
        justify-content: center;
        flex-direction: row;
        flex-wrap: wrap;
    }

    & .section {
        margin: 0 auto;
        width: 1288px;
    
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    
    
        @media (max-width: 1288px) {
            width: 640px;
        }
        @media (max-width: 600px) {
            width: 320px;
        }
    }
}