.div {
    display: inline-block !important;
    height: 25px;
    margin: 7.5px 5px;
    padding: 0 12px 2px 12px;

    border-radius: 0 5px 5px 0;

    display: flex;
    align-items: center;

    font-size: 10px;
    font-weight: 800;
    text-transform: uppercase;
    line-height: 25px;

    transition: all .2s linear;


    &:hover {
        cursor: pointer;
    }
}