$rwdMaxWidth: 600px;


.footer {
    margin: 40px 0 0 0;
    height: 40px;

    display: flex;
    align-items: center;


    & p {
        padding: 0 30px;

        font-size: 12px;


        @media (max-width: $rwdMaxWidth) {
            display: none;
        }
    }
    & nav {
        position: absolute;
        right: 30px;

        display: flex;
        align-items: center;

        
        & .icon {
            margin: 0 7px;

            opacity: 0.8;


            &:hover {
                opacity: 1;
            }
        }
        & #email {
            width: 30px;
        }
        & #linkedin {
            width: 23px;
        }
        & #github {
            margin: 0 7px 0 9px;
            width: 25px;
        }
        & #instagram {
            width: 27px;
        }
        & #blog {
            margin: -2px 7px 0 7px;
            width: 25px;

            opacity: 0.9;

            
            &:hover {
                opacity: 1;
            }
        }
    }
}