$rwdMaxWidth: 860px;


.div {
    margin: 20px auto;
    padding: 30px 0;
    width: 840px;

    display: flex;
    align-items: top;

    border-radius: 0 10px 10px 10px;
    background-color: #616164;


    @media (max-width: $rwdMaxWidth) {
        padding: 5px 0;
        width: unset;
        flex-direction: column-reverse;

        border-radius: 10px;
    }

    & div {
        margin: 0 20px;


        & .section {
            padding: 30px 0;


            @media (max-width: $rwdMaxWidth) {
                font-size: 10px;
            }

            & h2 {
                margin: 3px 0;
            }
            & hr {
                border: none;
                border-top: solid 0.1px white;
            }

            & ul {
                margin: 0;
                padding: 0;
                list-style-type: none;
        
        
                & h3, & h4 {
                    margin: 15px 0 0 0;
                }
                & h5 {
                    margin: 0;

                    font-size: 18px;
                    font-weight: 300;


                    @media (max-width: $rwdMaxWidth) {
                        font-size: 10px;
                    }
                }
                & h6 {
                    margin: 0 0 10px 0;

                    font-weight: 400;
                }
                & span {
                    display: block;
                }
            }

            & .pointList {
                margin: 0 30px;
                list-style-type: disc;
            }


            & .image {
                position: absolute;
                width: 80px;

                background-color: white;
                border-radius: 10px;


                @media (max-width: $rwdMaxWidth) {
                    margin: 5px 0;
                    width: 60px;
                }
            }
            & .bypassImage {
                margin: 0 0 0 100px;


                @media (max-width: $rwdMaxWidth) {
                    margin: 0 0 0 80px;
                }
            }
        }
    }
    
    & .aside {
        width: 280px;


        @media (max-width: $rwdMaxWidth) {
            width: unset;
        }
    }
}