$rwdMaxWidth: 630px;


.div {
    margin: 40px auto;

    border: solid 2px #7597c1;
    border-radius: 10px;
    box-shadow: 10px 10px 10px rgb(26, 26, 26);

    transition: border .2s linear;

    
    @media (max-width: $rwdMaxWidth) {
        margin: 30px auto;
    }

    &:hover {
        border-color: #a9d0ff;
    }

    & #main {
        width: 600px;
        height: 300px;


        @media (max-width: $rwdMaxWidth) {
            width: 300px;
            height: unset;
        }
        
        & img, & article {
            position: absolute;
            width: 600px;
            height: 300px;

            border-radius: 10px 10px 0 0;


            @media (max-width: $rwdMaxWidth) {
                position: static;
                width: 300px;
                height: unset;
            }
        }
        & article {
            background-color: #56565a;
            opacity: 0.98;

            cursor: unset;


            @media (max-width: $rwdMaxWidth) {
                background-color: transparent;
            }
            
            & button {
                position: absolute;
                margin: 30px 5px;
                padding: 5px 10px;

                color: white;
                background-color: #66666b;
                border-radius: 0 5px 5px 5px;
                border: none;

                font-size: 20px;
                font-weight: 900;
                font-family: Andale Mono, monospace;

                cursor: pointer;


                &:hover {
                    background-color: #727277;
                }
            }
            & h2{
                margin: 30px 50px 20px 50px;


                @media (max-width: $rwdMaxWidth) {
                    margin: 15px 20px;
                    font-size: 15px;
                }
            }
            & p {
                margin: 5px 50px;


                @media (max-width: $rwdMaxWidth) {
                    margin: 5px 20px;

                    font-size: 12px;
                }
            }

            & div {
                margin: 10px 50px;
                position: absolute;
                bottom: 0px;
                display: flex;
                align-items: center;


                @media (max-width: $rwdMaxWidth) {
                    margin: 20px 20px;
                    position: static;
                    
                    font-size: 12px;
                }

                & span {
                    bottom: 10px;
                    margin: 0 10px 0 0;
                }
            }
        }
    }

    & #secondary {
        height: 42px;

        border-radius: 0 0 10px 10px;

        background-color: #646468;


        @media (max-width: $rwdMaxWidth) {
            height: 84px;
        }

        & .HorizontalArrangement {
            display: flex;
            align-items: center;
        }
    
        & #left {
            margin: 0 10px;
            float: left;


            @media (max-width: $rwdMaxWidth) {
                float: right;
            }
        }
        & #right {
            margin: 0 10px;
            float: right;


            @media (max-width: $rwdMaxWidth) {
                float: right;
            }

            & a {
                height: 30px;
                margin: 5px 3px;
                padding: 0 12px 2px 12px;

                display: flex;
                align-items: center;

                border-radius: 0 5px 5px 5px;

                background-color: #7d7d83;
                color: white;

                text-decoration: none;

                transition: all .2s linear;


                &:hover {
                    background-color: #898991;
                }
            }
        }
    }
}