$rwdMaxWidth: 600px;


.div {
    margin: 0 auto;
    width: 400px;


    @media (max-width: $rwdMaxWidth) {
        width: 300px;
    }

    & section {
        margin: 0 0 40px 0;
        text-align: center;


        @media (max-width: $rwdMaxWidth) {
            font-size: 12px;
        }

        & h2 {
            margin: 0 0 5px 0;
        }
    }

    & .link {
        display: block;
        margin: 10px 0 10px -4px;
        position: relative;
        width: 400px;
        height: 100px;

        overflow: hidden;
        border: solid 4px rgba(0, 0, 0, 0);
        border-radius: 0 10px 10px 10px;

        cursor: pointer;

        transition: border .15s;


        @media (max-width: $rwdMaxWidth) {
            width: 300px;
            height: 60px;
        }

        &:hover {
            border: solid 4px white;
        }

        & p {
            position: absolute;
            left: 30px;
            bottom: -10px;

            font-size: 20px;
            font-weight: 500;

            color: white;


            @media (max-width: $rwdMaxWidth) {
                font-size: 15px;
            }
        }
    }
    & #linkEmail {
        background-color: #a4c1ff;
    }
    & #linkLinkedin {
        background-color: #d2abff;
    }
    & #linkInstagram {
        background-color: #f693ff;
    }

    & .icon {
        position: absolute;
        opacity: 0.6;
    }
    & #email {
        right: 20px;
        top: -40px;
        width: 200px;
        opacity: 0.7;


        @media (max-width: $rwdMaxWidth) {
            right: 30px;
            top: -25px;
            width: 120px;
        }
    }
    & #linkedin {
        right: 45px;
        top: -25px;
        width: 150px;


        @media (max-width: $rwdMaxWidth) {
            right: 45px;
            top: -15px;
            width: 90px;
        }
    }
    & #instagram {
        right: 33px;
        top: -30px;
        width: 170px;


        @media (max-width: $rwdMaxWidth) {
            right: 38px;
            top: -20px;
            width: 103px;
        }
    }
}