$rwdMaxWidth: 600px;


.header {
    margin: 0 0 30px 0;
    height: 80px;
    display: flex;
    align-items: center;


    & a {
        text-decoration: none;
        color: white;
    }

    & #logo {
        display: block;
        width: 200px;


        @media (max-width: $rwdMaxWidth) {
            display: none;
        }

        & img {
            margin: 20px;
            width: 40px;
        }

        & span {
            position: absolute;
            top: 22px;

            font-size: 20px;
            font-weight: 800;
            font-family: Comic Sans MS, Comic Sans, cursive;
        }
    }

    & nav {
        position: absolute;
        right: 32px;


        @media (max-width: $rwdMaxWidth) {
            margin: 20px auto;
            position: static;
            width: 280px;
        }

        & a {
            padding: 5px 10px;

            // 去除 <Link> 的底線
            text-decoration: none;


            @media (max-width: $rwdMaxWidth) {
                padding: 5px 5px;
            }

            span {
                font-size: 20px;
                font-weight: 700;
                
                transition: color .2s linear, border-radius 1s ease-in 1s;


                @media (max-width: $rwdMaxWidth) {
                    font-size: 16px;
                }
            }

            .pivot {
                font-size: 18px !important;
                
                color: white;


                @media (max-width: $rwdMaxWidth) {
                    font-size: 14px !important;
                }
            }
        }
        & #home {
            color: #c5d5ff;

            &:hover {
                color: #80a4ff;
            }
        }
        & #about {
            color: #ebcdff;

            &:hover {
                color: #d79bff;
            }
        }
        & #resume {
            color: #fdcbff;

            &:hover {
                color: #fc97ff;
            }
        }
        & #contact {
            color: #ffcee1;

            &:hover {
                color: #ff93bc;
            }
        }
    }
}